.advantages-about{
  width: 284px;
  min-height: 157px;
  margin-left: 18px;
  margin-right: 18px;
  margin-top: 32px;
  margin-bottom: 32px;
  text-align: center;
}

.advantages__title--about{
  color: #000000;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 35px;
}

.advantages__reasons{
  color: #333333;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
}

.advantages__text--white{
  color: #FFFFFF;
  background: #81B3D2;
  width: 320px;
  min-height: 191px;
  text-align: center;
  padding: 37px 18px;
  text-transform: uppercase;
}

.advantages__item{
  list-style-type: none;
}

.advantages__list{
  padding: 0;
  margin: 0;
  background: #EEEEEE;
}

.advantages__item{
  width: 320px;
  min-height: 260px;
  text-align: center;
  background: #EEEEEE;
}

.advantages-photo {
  display: flex;
  flex-shrink: 0;
  min-height: 260px;
  padding-bottom: 23px;
}

.advantages__text{
  padding: 23px 24px;
  text-transform: uppercase;
}

.advantages__description{
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;

}
